@use "global" as *;

.p-mv__bg {
  background: url(../images/mv_bg.jpg) no-repeat center/cover;
  width: 100%;
  height: 120vh;
}

.p-mv__catch {
  padding-top: myrem(125);
  text-align: center;
  position: relative;
  z-index: 2;
  @include mq(md) {
    padding-top: myrem(244);
  }
}

.p-mv__catch-title {
  font-size: myrem(26);
  font-weight: 700;
  line-height: calc(31 / 26);
  color: $white;
  @include mq(md) {
    font-size: myrem(62);
  }
}

.p-mv__catch-desc {
  margin-top: myrem(56);
  font-size: myrem(16);
  font-weight: 500;
  line-height: calc(26 / 16);
  color: $white;
  @include mq(md) {
    margin-top: myrem(75);
    font-size: myrem(26);
  }
}

.p-mv__phone {
  position: absolute;
  bottom: myrem(-330);
  left: 50%;
  transform: translateX(-50%);
  min-width: myrem(195);
  width: calc(518 / 1920 * 100%);
  @include mq(md) {
    max-width: initial;
    bottom: myrem(-550);
  }
}

.p-mv__phone img {
  aspect-ratio: 518 / 1055;
  object-fit: contain;
  width: 100%;
}
