@use "global" as *;

.p-member {
  background-color: $light-blue2;
}

.p-member__content {
  display: grid;
  gap: myrem(32);
  padding: myrem(30) 0 myrem(40);
  max-width: myrem(500);
  margin: auto;
  @include mq(md) {
    gap: myrem(48);
    padding: myrem(80) 0 myrem(90);
    max-width: 100%;
  }
}

.p-member__block {
  background-color: $white;
  padding: myrem(40) myrem(20);
  border-radius: myrem(18);
}

.p-member__block.p-member__block--grid {
  @include mq(md) {
    display: grid;
    grid-template-columns: 1fr 1.2fr;
    gap: myrem(28);
  }
}

.p-member__block:nth-child(1) {
  @include mq(md) {
    grid-row: 1 / 2;
    grid-column: 1 / 3;
  }
}

.p-member__block:nth-child(2) {
  @include mq(md) {
    grid-row: 2 / 3;
    grid-column: 1 / 2;
  }
}

.p-member__block:nth-child(3) {
  @include mq(md) {
    grid-row: 2 / 3;
    grid-column: 2 / 3;
  }
}

.p-member__text {
  margin-top: myrem(40);
}

.p-member__text p {
  font-size: myrem(16);
  font-weight: 300;
  line-height: 2;
  @include mq(md) {
    font-size: myrem(14);
  }
}

.p-member__message {
  margin-top: myrem(50);
  padding: myrem(30) myrem(20);
  background-color: $light-blue3;
  border-radius: myrem(18);
  @include mq(md) {
    margin-top: 0;
  }
}

.p-member__message p {
  font-size: myrem(16);
  font-weight: 300;
  line-height: 2;
  @include mq(md) {
    font-size: myrem(14);
  }
}
