@use "global" as *;

.p-company {
  background-color: $light-blue4;
}

.p-company__content {
  padding-top: myrem(50);
  padding-bottom: myrem(55);
  @include mq(md) {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    padding-top: myrem(100);
    padding-bottom: myrem(110);
  }
}

.p-company__map {
  margin-top: myrem(40);
  @include mq(md) {
    margin-top: 0;
  }
}

.p-company__map iframe {
  border: 0;
  filter: grayscale(1);
  width: min(100%,myrem(600));
  height: myrem(300);
  border-radius: myrem(8);
}
