@use "global" as *;

.p-vision__content {
  padding-top: myrem(60);
  padding-bottom: myrem(65);
  max-width: myrem(500);
  margin: auto;
  @include mq(md) {
    padding-top: myrem(113);
    padding-bottom: myrem(119);
    max-width: 100%;
  }
}

.p-vision__body {
  background-color: $blue;
  padding: myrem(50) myrem(20) myrem(50);
  border-radius: myrem(16);
  @include mq(md) {
    padding: myrem(95) myrem(80) myrem(100);
  }
}
