@use "global" as *;

.c-btn {
  font-size: myrem(20);
  font-weight: 700;
  line-height: calc(52 / 36);
  color: $blue;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: myrem(10) myrem(20) myrem(10);
  min-width: myrem(283);
  border: 1px solid $blue3;
  border-radius: myrem(38);
  overflow: hidden;
  transition: .2s ease-in-out;
}

.c-btn:hover {
  transform: rotateX(360deg);
  transition: .8s ease-in-out;
}
