@use "global" as *;

.p-product {
  background-color: $light-blue;
  position: relative;
}

.p-product__content {
  display: grid;
  @include mq(md) {
    grid-template-columns: repeat(2, 1fr);
  }
}

.p-product__img {
  margin-left: calc(50% - 50vw);
  width: 100vw;
  @include mq(md) {
    margin-left: 0;
    width: 100%;
  }
}

.p-product__img img {
  aspect-ratio: 960 / 720;
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.p-product__body {
  padding: myrem(50) myrem(24) myrem(40);
  text-align: center;
}

.p-product__sub-title {
  font-size: myrem(18);
  font-weight: 700;
  line-height: calc(52 / 36);
  color: $light-black;
  @include mq(md) {
    font-size: myrem(32);
  }
}

.p-product__text {
  margin-top: myrem(40);
  text-align: center;
}

.p-product__text p {
  font-size: myrem(14);
  font-weight: 400;
  line-height: 2;
  color: $light-black;
  @include mq(md) {
    font-size: myrem(16);
  }
}

.p-product__btn {
  margin-top: myrem(40);
}
