@use "global" as *;

.p-header {
  background-color: #0f1e56;
  height: myrem(70);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  width: 100%;
  @include mq(md) {
    height: myrem(90);
  }
}

.p-header__inner {
  height: inherit;
  padding-left: myrem(40);
  @include mq(md) {
    padding-right: myrem(40);
  }
}

.p-header__body {
  display: flex;
  align-items: center;
  height: inherit;
}

.p-header__logo {
  flex-shrink: 0;
  height: inherit;
}

.p-header__logo h1 {
  height: inherit;
}

.p-header__logo h1 a {
  font-size: myrem(20);
  font-weight: 700;
  line-height: calc(29 / 20);
  color: $white;
  height: inherit;
  display: flex;
  align-items: center;
}

.p-header__hamburger {
  height: inherit;
  background-color: $white;
  margin-left: auto;
  width: myrem(70);
  padding: myrem(10);
  @include mq(md) {
    display: none;
  }
}

.p-header__nav {
  display: none;
  position: absolute;
  top: myrem(70);
  left: 0;
  z-index: 10;
  width: 100%;
  background-color: $white;
  @include mq(md) {
    margin-left: auto;
    padding-left: myrem(20);
    height: inherit;
    display: block !important;
    position: initial;
    width: auto;
    background: none;
  }
}
