@use "global" as *;

.c-card__title {
  font-size: myrem(16);
  font-weight: 700;
  line-height: calc(52 / 36);
  color: $white;
  @include mq(md) {
    font-size: myrem(24);
    text-align: center;
  }
}

.c-card__text {
  margin-top: myrem(26);
  @include mq(md) {
    margin-top: myrem(52);
    text-align: center;
  }
}

.c-card__text p {
  font-size: myrem(14);
  font-weight: 400;
  line-height: 2;
  color: $white;
  @include mq(md) {
    font-size: myrem(20);
  }
}

.c-card__text p span {
  display: inline-block;
  margin-top: myrem(30);
  @include mq(md) {
    margin-top: 0;
  }
}
