@use "global" as *;

.p-nav__list {
  padding: myrem(20);
  @include mq(md) {
    display: flex;
    gap: myrem(60);
    height: inherit;
    padding: 0;
  }
}

.p-nav__item {
  height: inherit;
  padding: myrem(20) 0;
  @include mq(md) {
    padding: 0;
  }
}

.p-nav__item a {
  font-size: myrem(13);
  font-weight: 500;
  line-height: calc(16 / 13);
  color: #002d45;
  height: inherit;
  display: flex;
  align-items: center;
  justify-content: center;
  @include mq(md) {
    justify-content: start;
    color: $white;
  }
}
