@use "global" as *;

.c-title {
  font-size: myrem(21);
  font-weight: 700;
  line-height: calc(52 / 36);
  color: $white;
  position: relative;
  z-index: 1;
  padding-top: myrem(14);
  padding-bottom: myrem(15);
  @include mq(md) {
    font-size: myrem(36);
    padding-top: myrem(28);
    padding-bottom: myrem(32);
  }
}

.c-title::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100vw;
  height: 100%;
  margin-left: calc(50% - 50vw);
  background-color: $navy;
}
