@use "global" as *;

.c-detail__content {
  display: flex;
  gap: myrem(30);
}

.c-detail__img {
  flex-shrink: 0;
  width: myrem(100);
}

.c-detail__img img {
  aspect-ratio: 1 / 1;
  object-fit: contain;
  width: 100%;
}

.c-detail__list {
  flex-grow: 1;
  display: grid;
  gap: myrem(14);
}

.c-detail__name {
  font-size: myrem(25);
  font-weight: 500;
  line-height: calc(36 / 25);
}

.c-detail__class {
  font-size: myrem(25);
  font-weight: 300;
  line-height: calc(36 / 25);
}

.c-detail__sns {
  display: flex;
  align-items: center;
  gap: myrem(17);
}

.c-detail__sns a {
  width: myrem(33);
}

.c-detail__sns a {
  aspect-ratio: 33 / 34;
  object-fit: contain;
}
